body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

input {
    margin-bottom: 5%;
}

.search-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.navbar{
    display: inline-flex;
    align-items: center;
    justify-content: space-around;
    box-sizing: border-box;
    min-height: 75px;
    width: 100%;
    background-color: rgba(68, 68, 255, 0.966);
    color: white;
}

a {
    color: white;
    text-decoration: none;
    margin: 0px 10px;
}


.card {
    display: flex;
    flex-direction: row;
    border: solid black 2px;
    margin-bottom: 1%;
    padding: 5px;
    width: 90%;
    align-items: center;
}

.card-img{
    width: 25%;
}

.card img {
    max-width: 156px;
    max-height: 196px;
}

.card-info{
    box-sizing: border-box;
    padding: 5px 20px;
    display: flex;
    flex-direction: column;
    text-align: start;
    width: 65%
}

.card-btn {
    width: 10%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: end;
    min-height: 100%
}

.card-btn button {
    background-color: blue;
    color: white;
    min-width: 100%;
    min-height: 50px;
    border-radius: 10px;
}

.card-info a {
    color: blue;
}
